<template>
  <view class="work-container">
	
	<uni-search-bar @confirm="search" placeholder="请输入搜索内容" v-model="params.projectName" radius="100" @clear="clear">
	</uni-search-bar>
	<view class="uni-padding-wrap uni-common-mt">
		<uni-segmented-control :current="current" :values="tabs" style-type="button"
			active-color="#007aff" @clickItem="onClickItem" />
	</view>
	<view class="content">
		<view v-for="(item, index) in list" :key="index">
			<uni-card :title="`${item.projectNo}${item.taskNo}`" :extra="item.nodeName" margin="10rpx" padding="10rpx" spacing="10rpx" is-full>
				<uni-row class="font-9">
					任务编号：{{item.projectNo}}{{item.taskNo}}
				</uni-row>
				<uni-row class="font-9">
					项目名称：{{item.projectName}}-{{item.taskType}}
				</uni-row>
				<uni-row class="font-9">
					任务节点：{{item.nodeName}}
				</uni-row>
				<uni-row class="font-9">
					流程类别：{{item.processDefinitionName}}
				</uni-row>
				<uni-row class="font-9">
					流程发起人：{{item.applyPerson}}
				</uni-row>
				<uni-row class="font-9">
					<uni-tag text="查看进度" type="success" v-if="checkPermi(['business:project:list'])" v-on:click="handleView(item.processInstanceId)" />
					<!-- <uni-tag text="办理" type="primary" v-if="checkPermi(['business:project:edit'])" v-on:click="handleUpdate(item.processInstanceId)" /> -->
				</uni-row>
			</uni-card>
		</view>
		<!-- <uni-table ref="table" :loading="loading" border stripe emptyText="暂无更多数据" @selection-change="selectionChange">
			<uni-tr>
				<uni-th width="100" align="center">任务编号</uni-th>
				<uni-th align="center">项目名称</uni-th>
				<uni-th width="100" align="center">任务节点</uni-th>
				<uni-th width="100" align="center">流程类别</uni-th>
				<uni-th width="100" align="center">流程发起人</uni-th>
				<uni-th width="150" align="center">操作</uni-th>
			</uni-tr>
			<uni-tr v-for="(item, index) in list" :key="index">
				<uni-td align="center">{{item.projectNo}}{{item.taskNo}}</uni-td>
				<uni-td align="left">
					{{item.projectName}}-{{item.taskType}}
				</uni-td>
				<uni-td align="center">
					{{item.nodeName}}
				</uni-td>
				<uni-td align="center">
					{{item.processDefinitionName}}
				</uni-td>
				<uni-td align="center">
					{{item.applyPerson}}
				</uni-td>
				<uni-td>
					<uni-tag text="查看" type="success" v-if="checkPermi(['business:project:list'])" v-on:click="handleView(item.processInstanceId)" />
					<uni-tag text="办理" type="primary" v-if="checkPermi(['business:project:edit'])" v-on:click="handleUpdate(item.processInstanceId)" />
				</uni-td>
			</uni-tr>
		</uni-table> -->
		
		<uni-pagination :total="total" :current="params.pageNum" :pageSize="params.pageSize" :showIcon="true" @change="pageChange" />
		<uni-fab ref="fab" :pattern="pattern" horizontal="left" vertical="bottom"
			direction="horizontal" @fabClick="fabClick" />
		<!-- 提示窗 -->
		<uni-popup ref="alertDialog" type="dialog">
			<uni-popup-dialog :type="alertType" cancelText="取消" confirmText="确认" title="提示" :content="alertContent" @confirm="dialogConfirm"
				@close="dialogClose"></uni-popup-dialog>
		</uni-popup>
		<!-- 提示信息弹窗 -->
		<uni-popup ref="message" type="message">
			<uni-popup-message :type="msgType" :message="messageText" :duration="2000"></uni-popup-message>
		</uni-popup>
	</view>
  </view>
</template>

<script>
import {getTaskWaitByPage,getTaskFinishByPage} from "@/api/work/business/projectTask";
export default {
  data () {
    return {
		// 遮罩层
		loading : false,
		tabs: ['待办', '已办'],
		current: 0,
		projectRegionOptions: [],
		// 总条数
		total: 0,
		// 查询参数
		params: {
			pageNum: 0,
			pageSize: 6,
			projectName: undefined,
			projectNo: undefined,
			applyPerson:undefined
		},
		// table 数据
		list: [],
		// 悬浮按钮
		pattern: {
			color: '#7A7E83',
			backgroundColor: '#fff',
			selectedColor: '#007AFF',
			buttonColor: '#007AFF',
			iconColor: '#fff'
		},
		// 弹出框
		alertType:'',
		alertContent:'',
		// 消息提示窗口、提示框
		msgType: '',
		messageText: '',
    }
  },
  created () {
  	this.getList();
  },
  onShow(){
	this.getList();
  },
  methods: {
	onClickItem(e) {
		if (this.current !== e.currentIndex) {
			this.current = e.currentIndex
			this.params.pageNum = 0;
			this.getList();
		}
	},
	// 搜索框
	search() {
		this.params.pageNum = 0;
		this.getList()
	},
	// 翻页
	pageChange(e){
		this.params.pageNum = e.current;
		this.getList()
	},
	// 类型选项变更事件
	typeChange(){
		this.getList()
	},
	// 加载数据
	loadData () {
	  this.params.pageNum += 1;
	  this.getList();
	},
	clear() {
		this.params.pageNum = 0;
		this.params.projectName = "";
		this.getList();
	},
	// 查询列表
	getList () {
		const app = this
		app.loading =true
		if(this.current==0){//待办
			getTaskWaitByPage(this.params).then(res => {
				app.list = res.rows;
				app.total = res.total;
				app.loading =false
			})
		}else{//已办
			getTaskFinishByPage(this.params).then(res => {
				app.list = res.rows;
				app.total = res.total;
				app.loading =false
			})
		}
		
	},
	// 悬浮按钮
	fabClick() {
		uni.navigateTo({ url: '/pages/work/business/project/edit?flag='+ 1})
	},
	handleUpdate(id){
		uni.navigateTo({ url: '/pages/work/business/project/edit?id=' + id })
	},
	handleView(id){
		uni.navigateTo({ url: '/pages/work/business/projectTask/history?id=' + id })
	},
	handleDelete(id){
		// this.alertType = 'warn'
		// this.alertContent ='是否确认删除该条数据?'
		// this.$refs.alertDialog.open()
		// this.tempId = id;
		// 禁止删除
		this.msgType = 'error'
		this.messageText = '请联系管理员进行删除！'
		this.$refs.message.open()
	},
	// 确认删除
	dialogConfirm(){
		const app = this
		if(app.tempId){
			delProject(app.tempId).then(() => {
				app.getList();
			});
		}
	},
	// 取消删除
	dialogClose(){
		this.tempId ="";
	},
  }
}
</script>

<style lang="scss" scoped>
.list-item {
  padding: 20rpx 0;
  border-bottom: 0.5px solid #ccc;
}
</style>